  created() {
    this.$store.commit("setShowlefttree", true);
    this.multipointDateRange = [
      new Date(new Date().setDate(new Date().getDate() - 10)),
      new Date()
    ];
    this.dateRangeJ = [
      new Date(new Date().setDate(new Date().getDate() - 10)),
      new Date()
    ];
    this.dateRange = [
      new Date(new Date().setDate(new Date().getDate() - 10)),
      new Date()
    ];
    this.dateRangeBY = [
      new Date(new Date().setDate(new Date().getDate() - 10)),
      new Date()
    ];
    //拷贝弹窗多点线性核查质控参数信息数据
    this.resetDailyQualityTable = JSON.parse(
      JSON.stringify(this.dailyQualityTable)
    );

    //拷贝集成干预测试质控参数信息
    this.resetInterventionData = JSON.parse(
      JSON.stringify(this.interventionList)
    );

    //拷贝加标回收率参数信息
    this.resetRecoveryList = JSON.parse(JSON.stringify(this.recoveryList));
  },
  methods: {
    //获取月质控 =>多点线性核查质控参数信息
    getCurrentData() {
      this.$http
        .get(
          "/Quality/qualityControlSet/currentMonthQualityControlMultipointParam",
          {
            params: {
              stationCodes: this.stationId
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.tableData = res.data.content.dataList;
          }
        });
    },
    //获取历史月质控 =>多点线性核查质控参数信息
    getHistoryCurrentData() {
      this.$http
        .get(
          "/Quality/qualityControlSet/historyMonthQualityControlMultipointParam",
          {
            params: {
              startTime: this.multipointDateRange[0].format("yyyy-MM-dd"),
              endTime: this.multipointDateRange[1].format("yyyy-MM-dd"),
              indexCodes: this.stateRadio,
              stationCodes: this.stationId
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.historyCurrentData = res.data.content.dataList;
            this.getWeeklyQualityControl(this.stateRadio,'MULTIPOINT_LINEARITY');
          }
        });
    },
    //获取月质控 =>集成干预核查质控参数信息
    integratedIntervention() {
      this.$http
        .get(
          "/Quality/qualityControlSet/currentMonthQualityControlIntegratedParam",
          {
            params: {
              stationCodes: this.stationId
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.integratedTable = res.data.content.dataList;
          }
        });
    },
    //获取历史月质控 =>集成干预核查质控参数信息
    getHistoryIntervention() {
      this.$http
        .get(
          "/Quality/qualityControlSet/historyMonthQualityControlIntegratedParam",
          {
            params: {
              startTime: this.dateRangeJ[0].format("yyyy-MM-dd"),
              endTime: this.dateRangeJ[1].format("yyyy-MM-dd"),
              indexCodes: this.stateRadioJ,
              stationCodes: this.stationId
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.integratedTableHis = res.data.content.dataList;
            this.getWeeklyQualityControl(this.stateRadioJ,'INTEGRATED_INTERVENTION');
          }
        });
    },

    //获取月质控 =>实际水样对比质控参数信息
    waterSamples() {
      this.$http
        .get(
          "/Quality/qualityControlSet/currentMonthQualityControlRealWaterParam",
          {
            params: {
              stationCodes: this.stationId
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.sampleRatioTable = res.data.content.dataList;
          }
        });
    },

    //获取月质控 =>加标回收率质控参数信息
    recoveryRateAddition() {
      this.$http
        .get(
          "/Quality/qualityControlSet/currentMonthQualityControlRecoveryParam",
          {
            params: {
              stationCodes: this.stationId
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.recoveryRateTable = res.data.content.dataList;
          }
        });
    },
    //获取历史月质控 =>加标回收率质控参数信息
    historyIntegratedParam() {
      this.$http
        .get(
          "/Quality/qualityControlSet/historyMonthQualityControlRecoveryParam",
          {
            params: {
              startTime: this.dateRange[0].format("yyyy-MM-dd"),
              endTime: this.dateRange[1].format("yyyy-MM-dd"),
              indexCodes: this.stateRadioAdd,
              stationCodes: this.stationId
            }
          }
        )
        .then(res => {
          console.log(res);
          if (res.data.code === 0) {
            this.recoveryRateTableHis = res.data.content.dataList;
            this.getWeeklyQualityControl(this.stateRadioAdd,'RECOVERY');
          }
        });
    },
    //设置月质控 =>多点线性核查质控参数信息 单因子提交
    singleFactorSubmission(item, index) {
      let list = [
        {
          stationCodes: this.stationId,
          indexCode: item.itemCode,
          sourceValues: [],
          pointValues: []
        }
      ];
      this.dailyQualityTable.forEach(n => {
        list[0].sourceValues.push(n[item.prop].state);
        list[0].pointValues.push(parseFloat(n[item.prop].value));
      });
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlMultipointParam",
          list
        )
        .then(res => {
          if (res.data.code === 0) {
            this.getCurrentData();
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.resetDailyQualityTableData();
          }
        });
    },
    //重置多点线性核查质控参数信息数据
    resetDailyQualityTableData() {
      this.dailyQualityTable = JSON.parse(
        JSON.stringify(this.resetDailyQualityTable)
      );
    },
    //选择 零点核查信息-跨度核查信息
    selectZeroPoint(item, index) {
      this.linearZeroPoint = true;
      this.paramZeroDrift(item.code);
      this.paramSpanDrift(item.code);
      this.selectPointData.prop = item.code;
      this.selectPointData.number = index;
    },
    //获取月质控多点线性核查质控设置参数内零点核查信息
    paramZeroDrift(code) {
      this.$http
        .get(
          "/Quality/qualityControlSet/monthQualityControlMultipointParamZeroDrift",
          {
            params: {
              stationCodes: this.stationId,
              indexCodes: code
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.zeroCheckTable = res.data.content.dataList;
          }
        });
    },
    //获取月质控多点线性核查质控设置参数内跨度核查信息
    paramSpanDrift(code) {
      this.$http
        .get(
          "/Quality/qualityControlSet/monthQualityControlMultipointParamSpanDrift",
          {
            params: {
              stationCodes: this.stationId,
              indexCodes: code
            }
          }
        )
        .then(res => {
          if (res.data.code === 0) {
            this.spanVerification = res.data.content.dataList;
          }
        });
    },
    //选择零点核查信息
    selectZeroData(item) {
      let code = this.selectPointData.prop;
      let num = this.selectPointData.number;
      for (let i in this.dailyQualityTable[num]) {
        if (
          this.dailyQualityTable[num][i].code &&
          this.dailyQualityTable[num][i].code === code
        ) {
          this.dailyQualityTable[num][i].state = "零点核查";
          this.dailyQualityTable[num][i].value = item.value;
        }
      }
      this.linearZeroPoint = false;
    },
    //选择跨度核查信息
    selectSpanData(item) {
      let code = this.selectPointData.prop;
      let num = this.selectPointData.number;
      for (let i in this.dailyQualityTable[num]) {
        if (
          this.dailyQualityTable[num][i].code &&
          this.dailyQualityTable[num][i].code === code
        ) {
          this.dailyQualityTable[num][i].state = "跨度核查";
          this.dailyQualityTable[num][i].value = item.value;
        }
      }
      this.linearZeroPoint = false;
    },
    //项目设置详情=>表单数据改变时将绑定数据的状态变为‘标样核查’
    changeMonitoringItems(item) {
      item.state = "标样核查";
    },
    //设置月质控集成干预核查质控参数信息
    submitIntegrated(item) {
      let prop = item.prop;
      let param = {
        stationCodes: this.stationId,
        indexCode: item.itemCode,
        notIntegratedTime: this.interventionList[0][prop].format(
          "yyyy-MM-dd hh:00:00"
        ),
        overIntegratedTime: this.interventionList[1][prop].format(
          "yyyy-MM-dd hh:00:00"
        )
      };
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlIntegratedParam",
          [param]
        )
        .then(res => {
          if (res.data.code === 0) {
            this.resetIntegrated();
            this.integratedIntervention();
            this.$message({
              message: res.data.message,
              type: "success"
            });
          }
        });
    },
    //批量设置月质控集成干预核查质控参数信息
    batchSubmitIntegrated() {
      let list = [];
      this.dailyTableHead.forEach(item => {
        let param = {
          stationCodes: this.stationId,
          indexCode: item.itemCode,
          notIntegratedTime: this.interventionList[0][item.prop].format(
            "yyyy-MM-dd hh:00:00"
          ),
          overIntegratedTime: this.interventionList[1][item.prop].format(
            "yyyy-MM-dd hh:00:00"
          )
        };
        list.push(param);
      });
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlIntegratedParam",
          list
        )
        .then(res => {
          if (res.data.code === 0) {
            this.resetIntegrated();
            this.integratedIntervention();
            this.integrated = false;
            this.$message({
              message: res.data.message,
              type: "success"
            });
          }
        });
    },
    //重置月质控集成干预核查质控参数信息
    resetIntegrated() {
      this.interventionList = JSON.parse(
        JSON.stringify(this.resetInterventionData)
      );
    },
    //设置月质控实际水样对比质控参数信息
    setWaterSampleDate() {
      let param = {
        stationCodes: this.stationId,
        // indexCode:'',
        testTime: this.waterSampleDate.format("yyyy-MM-dd hh:00:00")
      };
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlRealWaterParam",
          [param]
        )
        .then(res => {
          if (res.data.code === 0) {
            this.waterSampleDate = "";
            this.waterSample = false;
            this.waterSamples();
            this.$message({
              message: res.data.message,
              type: "success"
            });
          }
        });
    },
    //单个设置月质控加标回收率质控参数信息
    submitRecoveryRate(item, index) {
      let param = {
        stationCodes: this.stationId,
        indexCode: item.itemCode,
        addConcentration: this.recoveryList[0][item.prop],
        constantVolume: this.recoveryList[1][item.prop]
      };
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlRecoveryParam",
          [param]
        )
        .then(res => {
          if (res.data.code === 0) {
            this.recoveryRateAddition();
            this.resetRecoveryListData();
            this.recoveryRate = false;
            this.$message({
              message: res.data.message,
              type: "success"
            });
          }
        });
    },
    //重置月质控加标回收率质控参数信息
    resetRecoveryListData() {
      this.recoveryList = JSON.parse(JSON.stringify(this.resetRecoveryList));
    },
    //批量设置月质控加标回收率质控参数信息
    batchSubmitRecoveryRate() {
      let list = [];
      this.dailyTableHead.forEach((item, index) => {
        let param = {
          stationCodes: this.stationId,
          indexCode: item.itemCode,
          addConcentration: this.recoveryList[0][item.prop],
          constantVolume: this.recoveryList[1][item.prop]
        };
        list.push(param);
      });
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlRecoveryParam",
          list
        )
        .then(res => {
          if (res.data.code === 0) {
            this.recoveryRateAddition();
            this.resetRecoveryListData();
            this.$message({
              message: res.data.message,
              type: "success"
            });
          }
        });
    },

    //转换月份
    setMonthConvert(time) {
      let date = parseInt(new Date(time).format("M"));
      let str = "";
      switch (date) {
        case 1:
          str = "一";
          break;
        case 2:
          str = "二";
          break;
        case 3:
          str = "三";
          break;
        case 4:
          str = "四";
          break;
        case 5:
          str = "五";
          break;
        case 6:
          str = "六";
          break;
        case 7:
          str = "七";
          break;
        case 8:
          str = "八";
          break;
        case 9:
          str = "九";
          break;
        case 10:
          str = "十";
          break;
        case 11:
          str = "十一";
          break;
        case 12:
          str = "十二";
          break;
      }
      return str + "月";
    },
    //标样核查设置单个因子提交
    submitDaily(item) {
      let param = {
        stationCodes: this.stationId, //this.stationId,
        indexCode: item.itemCode,
        spanValue: this.dailyQualityTableBY[1][item.prop],
        standardValue: this.dailyQualityTableBY[2][item.prop],
       
      };
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlStandardCheckParam",
          [param]
        )
        .then(res => {
          if (res.data.code === 0) {
            this.dailyQualityTableBY[1][item.prop] = "";
            this.dailyQualityTableBY[2][item.prop] = "";
            this.getByCurrentData();
            this.getByhistoryDayQuality()

            this.$message({
              message: res.data.message,
              type: "success"
            });
          }
        });
    },
    //标样核查设置批量提交
    submitDailyBatch() {
      let list = [];
      this.dailyTableHeadBY.forEach(item => {
        let param = {
          stationCodes: this.stationId,
          indexCode: item.itemCode,
          spanValue: this.dailyQualityTableBY[1][item.prop],
          standardValue: this.dailyQualityTableBY[2][item.prop],
        
        };
        list.push(param);
      });
      this.$http
        .post(
          "/Quality/qualityControlSet/editorCurrentMonthQualityControlStandardCheckParam",
          list
        )
        .then(res => {
          if (res.data.code === 0) {
            this.dailyBY = false;
            this.$message({
              message: res.data.message,
              type: "success"
            });
               this.getByCurrentData();
            this.getByhistoryDayQuality()
          }
        });
    },
    //获取标样核查当前使用24小时质控参数信息
    getByCurrentData(){
      
      this.$http.get("/Quality/qualityControlSet/currentMonthQualityControlStandardCheckParam", {
        params: {
          stationCodes:  this.stationId
        }
      }).then(res=>{
          if(res.data.code === 0){
            this.tableDataBYone = res.data.content.dataList;
          
          }
      })
    },
    //获取标样核查历史设置24小时质控参数信息
    getByhistoryDayQuality(){
  
      this.$http.get("/Quality/qualityControlSet/historyMonthQualityControlStandardCheckParam", {
        params: {
          startTime: this.dateRangeBY[0].format('yyyy-MM-dd'),
          endTime: this.dateRangeBY[1].format('yyyy-MM-dd'),
          indexCodes: this.stateRadioBY,
          stationCodes:  this.stationId
        }
      }).then(res=>{
        if(res.data.code === 0){
          this.tableDataBYtwo = res.data.content.dataList;
          this.getWeeklyQualityControl(this.stateRadioBY,'VERIFICATION_STANDARD_SAMPLES');
        }
      })
    },
    //转换水质文字
    conversionWaterQuality(tag){
        let num = parseInt(tag);
        let str = '';
        switch(num){
          case 1:
            str =  'Ⅰ类';
          case 2:
            str =  'Ⅱ类';
          case 3:
            str =  'Ⅲ类';
          case 4:
            str =  'Ⅳ类';
          case 5:
            str =  'Ⅴ类';
        }
        return str;
    },
    //获取当前水质
    getCurrentWaterQuality(){
      this.$http.get("/Quality/qualityControlSet/getStationCurrentWaterQualityData", {
        params: {
          stationCode:  this.stationId
        }
      }).then(res=>{
          if(res.data.code === 0){
            this.siteInfo.currentWaterQuality = this.conversionWaterQuality(res.data.content.info);
          }
      })
    },

    //获取集成干预、加标回收率、标样核查质控次数
    getWeeklyQualityControl(code,type){
      this.$http.get("/Quality/qualityControlSet/qualityControlConclusion", {
        params: {
          startTime: this.dateRange[0].format('yyyy-MM-dd'),
          endTime: this.dateRange[1].format('yyyy-MM-dd'),
          indexCodes: code,
          stationCodes:  this.stationId,
          qualityAnalysisType: type,
          ifCurrentSection: true
        }
      }).then(res=>{
        if(res.data.code === 0){
            if(type ==='MULTIPOINT_LINEARITY'){
              this.multipointInfo = res.data.content.info;
            }else if(type === 'INTEGRATED_INTERVENTION'){
              this.interventionInfo = res.data.content.info;
            }else if(type === 'RECOVERY'){
              this.rateRecoveryInfo = res.data.content.info;
            }else if(type === 'VERIFICATION_STANDARD_SAMPLES'){
              this.verificationInfo = res.data.content.info;
            }
        }
      })
    },
    //获取集成干预、加标回收率、标样核查温馨提示次数
    getWeeklyQualityControlTips(type){
      this.$http.get("/Quality/qualityControlSet/qualityControlTips", {
        params: {
          stationCodes:  this.stationId,
          qualityAnalysisType: type,
        }
      }).then(res=>{
        if(res.data.code === 0){
            console.log(1111,res);
            this.controlTips = res.data.content.info;
        }
      })
    },
  },
  computed: {
    totalPageNumber() {
      //计算总页数
      let number = 0;
      number = Math.ceil(this.total / this.pageSize);
      return number;
    },
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    }
  },
  watch: {
    stationInfo(value) {
      if (value.length > 0) {
        this.stationId = value[0].nodeId;

        this.siteInfo.nodeName = value[0].nodeName;//当前站点赋值
        this.siteInfo.targerWaterQuality = this.conversionWaterQuality(value[0].targerWaterQuality);//目标水质赋值
        this.siteInfo.areaName = value[0].areaName;//所属区域赋值
        this.siteInfo.basinName = value[0].basinName;//所属流域赋值

        this.getCurrentData();
        this.getHistoryCurrentData();
        this.integratedIntervention();
        this.getHistoryIntervention();
        this.waterSamples();
        this.recoveryRateAddition();
        this.historyIntegratedParam();
        this.getByCurrentData();
        this.getByhistoryDayQuality();
        this.getCurrentWaterQuality();

        this.getWeeklyQualityControlTips('MULTIPOINT_LINEARITY')
      }
    },
    tabActive(val){
      //多点线性  MULTIPOINT_LINEARITY
      //集成干预核查 INTEGRATED_INTERVENTION
      //实际水样  REAL_WATER_SAMPLE_COMPARISON
      //加标回收  RECOVERY
      //标样核查 VERIFICATION_STANDARD_SAMPLES
      switch(val){
        case 0:
          //stateRadio
          this.getWeeklyQualityControlTips('MULTIPOINT_LINEARITY');
          break;
        case 1:
          //stateRadioJ
          this.getWeeklyQualityControlTips('INTEGRATED_INTERVENTION');
          break;
        case 2:
          this.getWeeklyQualityControlTips('REAL_WATER_SAMPLE_COMPARISON');
          break;
        case 3:
          this.getWeeklyQualityControlTips('RECOVERY');
          break;
        case 4:
          this.getWeeklyQualityControlTips('VERIFICATION_STANDARD_SAMPLES');
          break;
      }
    }
  }
};
</script>

<style scoped lang="less">
.editInput {
  width: 50px;
  border: none;
  outline: none;
}
.modular-box {
  border: 1px solid #dce9fa;
  margin-top: 6px;
  .modular-header {
    height: 40px;
    line-height: 40px;
    padding: 0 8px;
    border-bottom: 1px solid #dce9fa;
    .title {
      font-size: 16px;
    }
    .title:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 3px;
      height: 10px;
      background: #166bce;
      margin-bottom: 3px;
      margin-right: 16px;
    }
  }
  .modular-body {
    min-height: 100px;
  }
  .toolbar {
    float: right;
    line-height: 38px;
    & > button {
      margin-bottom: 3px;
    }
  }
  .modular-row {
    padding: 6px;
  }
}
.currency-body-wrap {
  height: calc(100% - 52px);
  width: calc(100% - 154px);
  float: left;
}
.currency-aside-wrap {
  height: calc(100% - 52px);
  width: 148px;
  float: right;
  border: 1px solid #dce9fa;
  margin-top: 6px;
  padding: 6px;
  ul {
    border: 1px solid #cad9eb;
    li {
      height: 28px;
      line-height: 28px;
      padding: 0 6px;
      text-align: center;
      border-bottom: 1px solid #cad9eb;
      cursor: pointer;
      font-size: 12px;
      &:last-child {
        border-bottom: none;
      }
      &:hover {
        color: #2977d2;
      }
    }
    li.active {
      background: #e6f1fc;
      color: #2977d2;
    }
  }
}
.statistics-text {
  display: inline-block;
  vertical-align: top;
  position: relative;
  background: #e6f1fc;
  border: 1px solid #cad9eb;
  line-height: 36px;
  height: 36px;
  padding: 0 10px;
  .title {
    color: #186cce;
    font-size: 16px;
  }
  & > span {
    margin-right: 24px;
  }
  & > span:last-child {
    margin-right: 0;
  }
  &:before {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    border: 2px solid #166bce;
    border-right: none;
    border-bottom: none;
    top: -1px;
    left: -1px;
  }
  &:after {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    border: 2px solid #166bce;
    border-top: none;
    border-left: none;
    right: -1px;
    bottom: -1px;
  }
}
.week-row {
  padding: 10px 10px 0;
  /*height: 166px;*/
  font-size: 12px;
  .date-box {
    float: left;
    border: 1px solid #cad9eb;
    height: 146px;
    margin-right: 20px;
    text-align: center;
    width: 78px;
    padding-top: 52px;
    .month {
      font-size: 16px;
    }
  }
  .quality-control-list {
    float: left;
    max-width: calc(100% - 400px);
    & > li {
      float: left;
      border: 1px solid #cad9eb;
      height: 100%;
      margin-right: 20px;
      width: 148px;
      margin-bottom: 10px;
      & > div {
        padding: 0 10px;
        height: 28px;
        line-height: 28px;
        border-bottom: 1px solid #cad9eb;
        &:first-child {
          height: 33px;
          line-height: 32px;
          text-align: center;
          background: #e6f1fc;
          color: #2072d0;
        }
        &:last-child {
          border-bottom: none;
        }
      }
    }
  }
  .correlation-box {
    float: left;
    border: 1px solid #cad9eb;
    height: 146px;
    margin-right: 20px;
    text-align: center;
    padding: 0 8px;
    width: 80px;
    & > div {
      height: 50%;
      &:first-child {
        padding-top: 18px;
        border-bottom: 1px solid #d8d8d8;
      }
      .data {
        font-size: 15px;
      }
      .qualified,
      .unqualified {
        display: inline-block;
        vertical-align: top;
        width: 56px;
        height: 46px;
        line-height: 46px;
        text-align: center;
        margin-top: 13px;
      }
      .qualified {
        border: 1px solid #67c239;
        color: #67c239;
        background: #f0f9eb;
      }
      .unqualified {
        border: 1px solid #f56c6c;
        color: #f56c6c;
        background: #fef0f0;
      }
    }
  }
  .influence-box {
    float: left;
    border: 1px solid #cad9eb;
    height: 146px;
    text-align: center;
    width: 80px;
    padding-top: 52px;
    .data {
      font-size: 17px;
    }
  }
}
/* 日质控设置弹窗样式 */
.daily-quality-box {
  .daily-head-wrap {
    .row {
      padding-bottom: 10px;
      & > span {
        width: 50%;
        float: left;
      }
    }
  }
  .daily-table-wrap {
    .normal-table {
      th {
        text-align: center;
        height: 40px;
      }
      td {
        text-align: center;
        padding: 4px;
        height: 40px;
      }
    }
    .btn-wrap {
      text-align: center;
      padding-top: 10px;
    }
  }
}
.single-point-wrap {
  & > div {
    padding-bottom: 10px;
  }
}
.integrated-intervention {
  .normal-table {
    width: 100%;
    th {
      text-align: center;
      height: 40px;
    }
