  }
}
.recovery-rate {
  .normal-table {
    width: 100%;
    th {
      text-align: center;
      height: 40px;
    }
    td {
      text-align: center;
      padding: 4px;
      height: 40px;
    }
  }
  .btn-wrap {
    text-align: center;
    padding-top: 10px;
  }
}
.be-careful-text {
  text-align: left;
  color: red;
}
.company-text {
  width: 50px;
  display: inline-block;
  vertical-align: middle;
  text-align: left;
  padding-left: 4px;
}
</style>



